.navbar {
    button.navbar-toggle {
        padding: 6px 8px;
    }

    #navbar-search-collapse {
        padding: 0 15px;
        border-top: 1px solid #e7e7e7;
        margin: 0 -15px;

        form {
            padding: 8px 0;
        }
    }

    .avatar {
        display: inline-block;
        width: 32px;
        height: 32px;
        margin: -99px 0;
        vertical-align: middle;
        border-radius: 3px;
        background-size: cover;
        background-position: center;
    }
}
